import { Component, OnInit, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { LoadingController, NavController, ToastController, IonInput } from '@ionic/angular';
import { AngularFireAuth } from '@angular/fire/auth';
import { auth } from 'firebase/app';
import { User } from 'src/app/models/user.model';
import { Keyboard } from '@ionic-native/keyboard/ngx';

@Component({
  selector: 'app-login',
  templateUrl: './login.page.html',
  styleUrls: ['./login.page.scss'],
})
export class LoginPage implements OnInit {
  // Declara√ß√£o de vari√°veis
  public form: FormGroup;
  user: any;
  // N√£o fa√ßo ideia pra que isso serve, s√≥ sei que acho que tem haver com o keyboard
  @ViewChild('myInput', { static: true }) myInput: IonInput;

  constructor(
    private fb: FormBuilder,
    private loadingCtrl: LoadingController,
    private navCtrl: NavController,
    private toastCtrl: ToastController,
    private fbAuth: AngularFireAuth,
    public keyboard: Keyboard
  ) {
    // Atribui a vari√°vel form o valor do grupo de valores do FormBuilder da p√°gina HTML 
    this.form = this.fb.group({
      // Validators.required √© um par√¢metro para se o usu√°rio n√£o preencher a vari√°vel retornar um erro
      email: ['', Validators.required],
      password: ['', Validators.required],
    })
  }

  // Fun√ß√£o para quando a p√°gina for iniciada
  ngOnInit() {
    // Chama a fun√ß√£o 'verificaUser()'
    this.verificaUser();
  }

  // Fun√ß√£o para mostrar o keyboard
  showKeyboard() {
    this.myInput.setFocus();
  }

  // Fun√ß√£o para verificar se j√° existe um usu√°rio logado
  private async verificaUser() {
    // Atribui a vari√°vel user o resultado da seguinte consulta
    // Acessa o local storage e pega o valor do item 'app.user' e o transforma de um JSON para uma string
    this.user = JSON.parse(localStorage.getItem('app.user'));
    // Se o usu√°rio for diferente de nulo
    if (this.user != null) {
      // Fun√ß√£o para criar uma mensagem de carregando com a mensagem "Autenticando..."
      const loading = await this.loadingCtrl.create({ message: "Autenticando..." });
      // Mostra a mensagem na tela
      loading.present();
      // Navega para a p√°gina 'home'
      this.navCtrl.navigateRoot('home');
      // Fun√ß√£o que retira a mensagem de "Autenticando..."
      loading.dismiss();
    }
  }

  // Fun√ß√£o para o envio da autentica√ß√£o
  async submit() {
    // Fun√ß√£o para criar uma mensagem de carregando com a mensagem "Autenticando..."
    const loading = await this.loadingCtrl.create({ message: "Autenticando..." });
    // Mostra a mensagem na tela
    loading.present();

    // Fun√ß√£o que faz o login com email e senha, ela pega os valores do HTML
    this.fbAuth.auth.signInWithEmailAndPassword(this.form.controls['email'].value, this.form.controls['password'].value)
      // Quando pega os dados
      .then((data) => {
        // Fun√ß√£o que retira a mensagem de "Autenticando..."
        loading.dismiss();
        // Entra no local storage e "seta" o item 'app.user' com um JSON com os valores recebidos 
        localStorage.setItem('app.user', JSON.stringify(new User('', data.user.email, '')));
        // Navega para a p√°gina 'home'
        this.navCtrl.navigateRoot('home');
      })
      // Caso ocorra erro
      .catch((err) => {
        // Escreve no console a mensagem de erro
        console.log(err);
        // Fun√ß√£o que retira a mensagem de "Autenticando..."
        loading.dismiss();
        // Chama a fun√ß√£o 'showMessage()' e passa o par√¢metro "Usu√°rio ou senha inv√°lidos"
        this.showMessage("Usu√°rio ou senha inv√°lidos");
      });
  }

  // Fun√ß√£o para fazer login com o Google
  async signInWithGoogle() {
    // Fun√ß√£o que faz login com o Google
    this.fbAuth.auth.signInWithPopup(new auth.GoogleAuthProvider())
      // Quando pega os dados
      .then(async (data) => {
        // Escreve no console os dados
        console.log(data);
        // "Seta" no local storage um item com o nome 'app.user' com um JSON com os valores recebidos 
        localStorage.setItem('app.user', JSON.stringify(new User(data.user.displayName, data.user.email, data.user.photoURL)));
        // Fun√ß√£o para criar uma mensagem de carregando com a mensagem "Autenticando..."
        const loading = await this.loadingCtrl.create({ message: "Autenticando..." });
        // Mostra a mensagem na tela
        loading.present();
        // Navega para a p√°gina 'home'
        this.navCtrl.navigateRoot('home');
        // Fun√ß√£o que retira a mensagem de "Autenticando..."
        loading.dismiss();
      })
      // Caso ocorra erro
      .catch((err) => {
        // Escreve no console a mensagem de erro
        console.log(err);
        // Chama a fun√ß√£o 'showMessage()' e passa o par√¢metro "Usu√°rio ou senha inv√°lidos"
        this.showMessage("Usu√°rio ou senha inv√°lidos");
      });
  }

  // Fun√ß√£o para mostrar mensagem na tela que recebe o param√™tro message
  async showMessage(message: string) {
    // Aguarda a cria√ß√£o de um Toast Controller, com a mensagem passada no par√¢metro, com dura√ß√£o de 3 segundos
    await this.toastCtrl.create({ message: message, duration: 3000 })
      // Quando o Toast Controller √© terminado
      .then((toastData) => {
        // Escreve os dados no console
        console.log(toastData);
        // Mostra a mensagem na tela
        toastData.present();
      });
  }

  // Fun√ß√£o para ir a p√°gina de Singup
  async goToSignup() {
    // Navega para a p√°gina 'singup'
    this.navCtrl.navigateForward('signup');
  }
}
